import React from "react";
import moment from "moment";
import { useSelector } from "react-redux";

export default function All() {
  const histories = useSelector((state) => state.history.data);

  return (
    <ul className="list-group list-group-flush">
      {histories?.map((item, index) => {
        return (
          <div key={index}>
            <div className="px-1 py-2 border-bottom">
              <div className="d-flex align-items-stretch">
                <div
                  className="d-flex flex-column align-items-start justify-content-center border-end "
                  style={{ width: "80px" }}
                >
                  {item.type == "buy" && (
                    <img src="data:image/png;base64,"></img>
                  )}
                  {item.type == "referal" && (
                    <img src="data:image/png;base64,"></img>
                  )}
                  {(item.type === "won" ||
                    item.type === "lost" ||
                    item.type === "Game" ||
                    item.type === "cancelled") && (
                    <img src="data:image/png;base64,"></img>
                  )}
                  {item.type == "withdraw" && item.status == "success" && (
                    <span
                      className="text-capitalize mb-1 badge rounded-pill bg-success"
                      style={{
                        fontSize: "0.7rem",
                        marginLeft: "-4px",
                      }}
                    >
                      SUCCESS
                    </span>
                  )}
                  {item.type == "withdraw" && item.status == "pending" && (
                    <span
                      className="text-capitalize mb-1 badge rounded-pill bg-primary"
                      style={{
                        fontSize: "0.7rem",
                        marginLeft: "-4px",
                      }}
                    >
                      PENDING
                    </span>
                  )}
                  {item.type == "withdraw" && item.status == "declined" && (
                    <span
                      className="text-capitalize mb-1 badge rounded-pill bg-danger"
                      style={{
                        fontSize: "0.7rem",
                        marginLeft: "-4px",
                      }}
                    >
                      DECLINED
                    </span>
                  )}
                  <span className="text-start" style={{ fontSize: "0.8rem" }}>
                    {moment(item.createdAt).format("DD")}{" "}
                    {moment(item.createdAt).format("MMM")}
                  </span>
                  <span className="text-start" style={{ fontSize: "0.8rem" }}>
                    {moment(item.createdAt).format("hh:mm a")}
                  </span>
                </div>
                <div className="d-flex flex-column align-items-stretch justify-content-center flex-1 w-100 px-2">
                  <div className="d-flex align-items-stretch justify-content-between">
                    <div className="d-flex flex-column">
                      <span
                        className="fw-semibold text-capitalize text-start"
                        style={{ fontSize: "0.9rem" }}
                      >
                        {item.historyText}
                      </span>
                    </div>
                    <div className="d-flex flex-column align-items-end justify-content-center">
                      {(item.type == "withdraw" || item.type === "Game") && (
                        <span
                          className="text-danger text-end fw-bold my-1"
                          style={{ fontSize: "0.8rem" }}
                        >
                          (-) {item.amount}
                        </span>
                      )}
                      {(item.type == "buy" ||
                        item.type == "won" ||
                        item.type == "cancelled" ||
                        item.type == "referal") && (
                        <span
                          className="text-success text-end fw-bold my-1"
                          style={{ fontSize: "0.8rem" }}
                        >
                          (+) {item.amount}
                        </span>
                      )}
                    </div>
                  </div>
                  {item.type === "lost" ||
                  item.type === "won" ||
                  item.type === "Game" ||
                  item.type === "cancelled" ? (
                    <div className="d-flex align-items-center justify-content-between">
                      <span
                        className="text-start text-capitalize"
                        style={{ fontSize: "0.7rem" }}
                      >
                        Room Code : {item.roomCode}
                      </span>

                      <span
                        className="text-start"
                        style={{ fontSize: "0.7rem" }}
                      >
                        Closing Balance:{" "}
                        {item?.closingBalance?.toFixed(
                          item?.closingBalance % 1 !== 0 ? 2 : 0
                        )}
                      </span>
                    </div>
                  ) : (
                    <div className="d-flex align-items-center justify-content-between">
                      <span
                        className="text-start text-capitalize"
                        style={{ fontSize: "0.7rem" }}
                      >
                        {/* UTR: 301342543352 */}
                      </span>
                      <span
                        className="text-start"
                        style={{ fontSize: "0.7rem" }}
                      >
                        Closing Balance:{" "}
                        {item?.closingBalance?.toFixed(
                          item?.closingBalance % 1 !== 0 ? 2 : 0
                        )}
                      </span>
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        );
      })}
    </ul>
  );
}
